.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.app-header {
  max-height: 25%;
  background-color: black;
  height: 60px;
  padding: 8px;
}

.App-link {
  color: #61dafb;
}

.search-box {
  border-radius: 5px;
  padding: 8px;
  width: 95%;
  float: right;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
